import { isFunction, uniqBy } from 'lodash';
import { adaptCallbackForLegacyAPI } from '@devsnc/sn-controls-common';

export const actionHandlers = {
	'NOW_TYPEAHEAD_MULTI#VALUE_SET': ({ action, properties, dispatch }) => {
		const { value } = action.payload;
		const { onChange, onStagedValueChange, name } = properties;

		if (isFunction(onStagedValueChange)) {
			adaptCallbackForLegacyAPI(onStagedValueChange)({ value });
		}
		// faking event because app teams are expecting event as param in OnChange callback
		const event = {
			target: {
				value: value
			},
			persist: () => {
				return;
			},
			stopPropagation: () => {
				return;
			}
		};

		dispatch('NOW_RECORD_TYPEAHEAD_MULTIPLE#STAGED_VALUE_CHANGED', {
			name,
			value,
			event
		});

		if (isFunction(onChange)) {
			onChange(event);
		}
	},

	'NOW_TYPEAHEAD_MULTI#SELECTED_ITEMS_SET': ({
		action,
		properties,
		dispatch,
		updateProperties
	}) => {
		const { value } = action.payload;

		const items = value.map(({ id: value, label: displayValue }) => ({
			value,
			displayValue
		}));

		const { onValueChange, name } = properties;
		if (items) {
			if (isFunction(onValueChange)) {
				adaptCallbackForLegacyAPI(onValueChange)({
					value: uniqBy(items, 'value')
				});
			} else {
				updateProperties({ pills: uniqBy(items, 'value') });
			}

			dispatch('NOW_RECORD_TYPEAHEAD_MULTIPLE#VALUE_CHANGED', {
				value: uniqBy(items, 'value'),
				name
			});
		}
	},

	'NOW_TYPEAHEAD_MULTI#INTERACTION_FINISHED': ({
		properties: { onBlur },
		updateState
	}) => {
		if (isFunction(onBlur)) {
			onBlur(event);
		}
		updateState({
			initialSearchComplete: false
		});
	},

	'NOW_POPOVER#CONTENT_VISIBLE': ({ updateState, state }) => {
		const { onClick } = state.properties;
		const { initialSearchComplete } = state;

		if (isFunction(onClick) && !initialSearchComplete) {
			onClick();
			// This is done to prevent re-searching when popover opens again
			updateState({
				initialSearchComplete: true
			});
		}
	}
};
